body {
  background-color: #626C66;
}

.link {
  color: white;
}

.header-link {
  background-color: #444B47;
  min-height: 100px;
  display: flex;
  gap: 50px;
  justify-content: flex-end;
  align-items: center;
  padding-right: 50px;
  font-size: 18px;
  color: white;
  box-shadow: 0 3px 5px 0.5px rgb(14, 13, 13);
}

@media screen and (min-width: 200px) and (max-width: 600px) {
  .header-link {
    font-size: 16px;
    min-height: 80px;
    gap: 20px;
    justify-content: center;
    padding-right: 0px;
  }
}