.about-me {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70vh;
  padding: 30px;
  font-size: 20px;
  color: white;
  text-shadow: rgb(31, 31, 31) 0.1em 0.1em 0.3em;
}

@media screen and (min-width: 200px) and (max-width: 600px) {
  .about-me { 
    font-size: 18px;
    width: 300px;
    text-align: center;
    margin-top: 10px;
  }
}