.contact {
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70vh;
  padding: 50px;
  font-size: 20px;
  text-shadow: rgb(31, 29, 29) 0.1em 0.1em 0.3em;
  gap: 30px;
}

.linkedin-image {
  width: 10vh;
}

.linkedin {
  color: rgb(13, 13, 13);
  text-shadow: rgb(153, 149, 149) 0.1em 0.1em 0.3em;
  font-size: 20px;
}

.linkedin:hover {
  color: black;
  font-size: 25px;
}

.github-image {
  width: 10vh;
}

.github {
  color: rgb(13, 13, 13);
  font-size: 20px;
  text-shadow: rgb(153, 149, 149) 0.1em 0.1em 0.3em;
}

.github:hover {
  color: black;
  font-size: 25px;
}

.linkedin-first {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.github-second {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

@media screen and (min-width: 200px) and (max-width: 600px) {
  .contact {
    height: 70vh;
    padding: 20px;
    font-size: 18px;
  }

  .github-image {
    width: 7vh;
  }

  .linkedin-image {
    width: 7vh;
  }
}