.homePage {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 80vh;
}

.home {
  color: white;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:flex-start;
  height: 80%;
  padding: 30px;
}

.image {
  width: 250px;
  border-radius: 100%;
  border: 1px solid rgb(31, 29, 29);
  box-shadow: 3px 1px 8px rgb(26, 25, 25);
}

.title{
  color: white;
  text-shadow: #050505 2px 3px 2px;
}

.line1 {
  font-size: 30px;
  font-weight: 800;
  text-shadow: rgb(31, 29, 29) 0.1em 0.1em 0.3em;
}

.line2 {
  font-size: 22px;
  text-shadow: #050505 1px 1px 1px;
}

.description {
  text-align:right;
}

@media screen and (min-width: 200px) and (max-width: 600px) {
  .image {
    width: 180px;
    border-color: green;
  }

  .homePage {
    display: flex;
    flex-direction: column;
    height: auto;
    margin-top: 70px;
  }

  .home {
    color: white;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80%;
    padding: 10px;
    margin-right: 40px;
  }

  .line1 {
    font-size: 18px;
    font-weight: 800;
    text-shadow: rgb(31, 29, 29) 0.1em 0.1em 0.3em;
    width: 400px;
    text-align: end;
  }
  
  .line2 {
    font-size: 18px;
    text-shadow: #050505 1px 1px 1px;
    width: 300px;
    text-align: end;
  }

  .description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    text-align: right;
    padding-right: 30px;    
  }

  .title{
    text-align: right;
    width: 380px;
    font-size: 25px;
  }
}